import {Component} from '@angular/core';
import {DomSanitizer, SafeHtml} from '@angular/platform-browser';
import {RoutesService} from '../services/routes';

declare let window:any;

@Component({
    selector: 'markdown',
    template: `
        <div class="container">
          <div class="markdown-container" [innerHtml]="markdown">
        </div>
      `,
})
export class MarkdownComponent {
  markdown:SafeHtml;
  subscriptions:any[] = [];

  constructor(private routes:RoutesService, private sanitizer:DomSanitizer) {
    this.subscriptions.push(this.routes.currentRoute.subscribe(r => {
      if (r && r.markdown) {
        this.markdown = this.sanitizer.bypassSecurityTrustHtml(window.marked(r.markdown));
      }
    }));
  }

  ngOnDestroy() {
    this.subscriptions.forEach(s => s.unsubscribe());
  }
}



