import {Component, ViewChild} from '@angular/core';
import {OpenAPIService} from '../services/openapi';
import {UtilsService} from '../services/utils';
import {RoutesService} from '../services/routes';
import {MenuService} from '../services/menu';
import {TourService} from '../services/tour';

declare let window:any;

@Component({
    selector: 'console',
    templateUrl: '../views/console/main.pug',
    styles: [`
      .tour-button {
        position: fixed;
        bottom: 0;
        right: 0;
        border-radius: 0px;
        z-index: 10;
        cursor: pointer;
      }
      .tour-close-button {
        position: absolute;
        top: 0px;
        left: 0px;
        font-size: 8px;
        padding: 2px;
        width: 20px;
        height: 20px;
        color: white;
        text-align: left;
      }
    `]
})
export class ConsoleComponent {
  title:string = 'Console';
  apiCall:any;
  operationName:string;
  tagName:string;
  choices:any[];
  hideTourButton:boolean;
  @ViewChild('apiCallComponent') apiCallComponent;
  marked = window.marked;

  subscriptions:any[] = [];

  constructor(public openapi:OpenAPIService,
              public utils:UtilsService,
              private routes:RoutesService,
              private menu:MenuService,
              public tour:TourService) {
    window.consoleComponent = this;
    this.subscriptions.push(this.menu.choose.subscribe(choice => {
      if (choice) {
        if (choice.operation) this.setAPICall(choice.operation);
        else if (choice.tag) this.setChoices(choice);
      }
    }))
    this.subscriptions.push(this.openapi.spec.subscribe(spec => {
      this.setAPICall(this.operationName);
    }))
    this.subscriptions.push(this.routes.currentRoute.subscribe(route => {
      if (route && route.title) this.title = route.title;
    }));
  }

  ngOnDestroy() {
    this.subscriptions.forEach(s => s.unsubscribe());
  }

  setAPICall(name) {
    this.operationName = name;
    if (this.openapi.apiCalls) this.apiCall = this.openapi.apiCalls.filter(a => a.title === name)[0];
  }

  setChoices(choice) {
    this.tagName = choice.title || choice.tag;
    this.choices = choice.children;
  }
}



